"use client"
import React, { useState } from 'react'
import {useAuthState} from "react-firebase-hooks/auth"
import {auth} from '../firebase'
import { signOut } from 'firebase/auth'
import Link from 'next/link';

const Logged = () => {

  const [user] = useAuthState(auth);



  return (
  <div>
    {user ? (
      <>
       <button href="login" onClick={()=>{signOut(auth)}} className="border-2 cursor-pointer opacity-60 mx-5 duration-300 max-md:scale-70 hover:bg-blue-200/40 border-blue-200 ring-1 ring-black/40 p-1 rounded-2xl">Log Out</button>
      </>
    ) : (
      <Link href="login" className="border-2 cursor-pointer opacity-80 mx-5 duration-300 max-md:scale-65 hover:bg-blue-200/40 border-blue-200 ring-1 ring-black/40 p-1 px-3 rounded-2xl">Login</Link>
    )}
  </div>
  )
}

export default Logged